export default function SectionAbout({ className }) {
  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-about",
        className,
      ].join(" ")}
    >
      <h2 className="container section__title"> </h2>
      <div className="container">
        <div className="section-about__col-left">
          <div className="section__title"> </div>
          <p>
                    
                .{" "}
            <strong>   ,</strong>   
                    
             .
          </p>
          <p>
             ,  ,    
               ()   
              ,   .{" "}
            <strong>
                 ,    
                   
            </strong>{" "}
               .
          </p>
        </div>
        <div className="section-about__col-right">
          <img
            src={process.env.REACT_APP_PUBLIC_URL + "/section-about-img.png"}
            alt=""
          />
        </div>
      </div>
    </section>
  );
}
